html,body
{
    width: 100%;
    height: 100%;
    margin: 0;
    padding: 0;
    overflow-x: hidden; 

}


.menu-head {
    background: #000;
    padding-top: 40px;}
.logo {
    padding-left: 10px;
}

.logo img{
    width: 100%;  
    padding:20px;
}
.navbar-header button {
    width: 20%;
}
nav {
    padding-top: 10px;}
.navbar-nav{
    width: 100%;
}

.navbar-inverse  {
    background-image:none;
    background-color: #000;
    border: none;
    -webkit-box-shadow:none;
            box-shadow:none;
    padding-top: 18px;
    width: 100%;}
.navbar-nav li {
    width:16.6%;
    border-right: solid 1px #4cb847 ;
    text-align: center;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
}
ul.nav.navbar-nav
{
    width:100%;
}
.navbar-inverse .navbar-collapse {
    border: none;
    width: 90%;
    margin-right: -100px;
}
.navbar-nav>li>a {
   padding: 6px 10px 6px 2px;    
    font-size: 12px;
    color:#fff;
}

.navbar-inverse .navbar-nav>li>a{
    color: #fff;
    border-right: solid 1px #4cb847;
    position: relative;
}
.navbar-inverse .navbar-nav>li>a.active{
   color: #4cb847;  
}
.classes {
    color: red;
}
.navbar-inverse .navbar-nav>li>a:before{
    content: '';
    width: 0px;
    height: 1px;
    background: #4cb847;
    position: absolute;
    top: 100%;
    left: 0;
    -webkit-transition: .5s;
    -o-transition: .5s;
    transition: .5s;
}
.navbar-inverse .navbar-nav>li:hover a:before{ 
    width: 50%;
    -webkit-transform: translateX(100%);
        -ms-transform: translateX(100%);
            transform: translateX(100%);  
}
.navbar-inverse .navbar-nav>li:hover a{
    color: #4cb847;
}
.navbar-inverse .navbar-nav .active>a {
    background: #000;
    color:#4cb847;
}
.navbar-inverse .navbar-nav .active:hover a {
    background-image: none;
    color:#4cb847;
}
.call {
    padding-top: 20px;
    text-align: right;
}
.call button {
    background: #4cb847;
    border: none;
    border-right: solid 1px #000;
}
.call span {
    background: #4cb847;
    color: #000;
    padding: 5px;
}
.infoVideo {

    text-align: center;
    width: 100%;

}
video {
    width: auto;
    height: calc(100vh - 105px);
    border-left: solid 5px #000;
    border-right: solid 5px #000;   
    border-bottom: solid 5px #000; 

}
