@media only screen and (max-width:1740px) and (min-width:1301px){
    .container-fluid {
        width: 100%;
    }
    video {
        width: 100%;
        height: calc(100vh - 110px);
    }
    .navbar-inverse .navbar-nav>li>a{
        padding-right: 0 20px;
        font-size: 11px;}

    .navbar-inverse .navbar-collapse {
        width: 100%;
        margin-right: 0;
    }
    .logo {
        padding-left: 50px;
    }
    .logo img{
       
        padding: 23px 0px;
    }
    .call {
        padding-top: 20px;
        text-align: left;
    }

}


@media only screen and (max-width:1300px) and (min-width:1025px){
    .container-fluid {
        width: 100%;
    }
    video {
        height: 100%;  
        width: 100%; 
    }
    .navbar-inverse .navbar-nav>li>a{
        padding-right: 0 20px;
        font-size: 10px;}

    .navbar-inverse .navbar-collapse {
        width: 100%;
        margin-right: 0;
    }
    .logo {
        padding-left: 50px;
    }
    .logo img{
        width: 110%;
        padding: 23px 0px;
    }
    .call {
        text-align: center;
    }
    }
@media only screen and (max-width:1024px) {
    .container-fluid {
        width: 100%;
        padding: 0;
    }
    video {
        height: 100%; 
        width: 100%;   
    }
    .navbar-inverse .navbar-nav>li>a 
    {
        font-size: 8px;
        padding-left: 2px;
    }
    .navbar-inverse .navbar-collapse {
        border: none;
        width: 95%;
    }
    .logo {
        padding-left: 10px;
    }
    .logo img{
        padding: 25px 0 0 0;
    }
    .navbar-inverse .navbar-collapse {
        width: 100%;
        margin-right: 0;
    }
    .call {
        text-align: center;
    }
    .call button {
        font-size: 10px;
    }
    .menu {
        padding: 0;
    }
    }

@media only screen and (max-width:767px) {
.logo{
    width: 40%;
    float: left;
    padding: 20px 0 0 62px;
}
.video {
    height: 100%;
    width: 100%;    
}

.logo img {
    width: 80%;
}
.menu{
    width: 15%;
    float: right;
}
.menu-head {
    padding-top: 20px;
}
.call {
    float: right;
    padding-top: 35px;
    margin-right: -16px;
}
.navbar-inverse .navbar-collapse {
    width: 300%;
}
.navbar-nav li {
    width:80%;
    border-right: none ;
    border-bottom: solid 2px #4cb847;
    float: right;
}
.navbar-inverse .navbar-nav>li>a{
    border-right: none;
   
}
.navbar-header button {
    margin-right: 40px;
    width: auto;
}
.call span {
    margin-right: 20px;
}

}
@media only screen and (max-width:500px) {
    .logo img {
        width: 100%;
    }
    video {
        height: 100%;
        width: 100%;    
    }
    .logo{
        padding: 16px 0 0 25px;
    }
    .navbar-nav li {
        border-right: none ;
    }
    .call span {
        margin-right: 40px;
    }
    .menu-head {
        padding-top: 0;
    }

}


