
.yritys {
    padding: 20px 0;
}
.yritys h2 {
    color: #4cb847;
    font-weight: 800;
    padding-bottom: 0;
    margin-bottom: 0;
}
.yritys p {
    padding: 30px 0;
    font-size: 20px;
    font-style: italic;
}
.yritys hr {
    float: left;
    width: 20%;
}
.company hr {
    float: none;
    width: 50%;
    margin-top: 20px;
}

.top-box a{
    text-decoration: none;
    color: #4cb847;
    font-weight: 600;
}
.company {
    margin-bottom: 50px;
}
.top-box img {
    width: 20%;
    border-radius: 5px;
}
.top-box {
    text-align: center;
    padding-top: 50px;
}
.btm-box img {
    width: 30%;
}
.btm-box a{
    color: #e79529;
}
@media only screen and (max-width: 1023px) and (min-width: 769px) {
    .yritys p {
        font-size: 16px;
    }

}

@media only screen and (max-width: 768px) and (min-width: 600px) {
 .top-box img {
     width: 25% !important;
 }
 .btm-box img {
     width: 40% !important;
 }
}
@media only screen and (max-width: 768px) {
.yritys {
    padding: 20px;
}
.yritys h2 {
    font-size: 24px;
}
.yritys hr {
    float: left;
    width: 60%;
}
.yritys p {
    font-size: 12px;
}
.top-box img {
    width: 40%;
}
.btm-box img{
    width: 60%;
}




}





