footer {
    background-color:#4cb847; 
    width: 100%;  
}
.container-fluid {
    width: 80%;
}
.contact h2 {
	color: #000;
    padding-top: 0;
    padding-left: 20px;
    font-size: 24px;
}
.location span {
	color: #000;
    padding-right: 5px;
    padding-top: 7px;
}
.location p{
	font-weight: 600;
	padding-left: 5px;
}
.location {
	padding-top: 20px;
	padding-left:20px;
	
}
.social-network i{
    background: #000;
    color: #4cb847;
    padding: 5px;
    border-radius: 4px;

}
.social-network a{
    color:#000;


}
.social-network {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    margin: auto;
    -ms-flex-wrap: wrap-reverse;
        flex-wrap: wrap-reverse;
    -ms-flex-pack: distribute;
        justify-content: space-around;
    padding-top: 32px;
}
.copyrights {
    text-align: center;
    padding: 5px;
    font-weight: 600;
}

@media only screen and (max-width:1025px) and (min-width:768px) {
    .contact h2 {
        font-size: 20px;
        padding-left: 20px;
    }
    .location p{
        font-size: 12px;
    }
    .copyrights p {
        font-size: 11px;
    }
    .location {
        padding-top: 50px;
        padding-left: 10px;
    }

}

@media only screen and (max-width:767px) and (min-width:320px) {
    .location {
        padding-left: 100px;
        
    }
    .location p{
        font-size: 12px;
    }
    .contact h2{
        text-align: none;
        font-size: 16px;
    }

    .location.social-network {
        width: 100%;
        padding-left: 0 !important;
    }
    .copyrights p {
        font-size: 12px;
    }
    .location.social-network i {
        width: 100% !important;
        padding-bottom: 2px !important;
    }


}

@media only screen and (min-height:1200px) {
footer {
    position: absolute;
    bottom: 0;
    left: 0;
    right: 0;
    margin: -150px;
    margin-left: 0;
    margin-right: 0;
}

}


