.rekry-head {
    position: relative;
    overflow: hidden;
}
.rekry-head:before {
    content: ' ';
    display: block;
    position: absolute;
    left: 0;
    top: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    opacity: 0.6;
    background-image: url('./images/rekry.jpg');
    background-repeat: no-repeat;
    background-position: 50% 0;
    -ms-background-size: cover;
    background-size: cover;
}
.rekry-head h2,h1 {
    text-align: center;
    z-index: 2;
    position: relative;
    color: #4cb847;
    background: #000;
    width: 30%;
    opacity: 0.7;
} 
.rekry-head h1{
    margin-top: 250px;
}
.rekry-head h2 {
    margin-bottom: 100px;
}
.rekry-btm h2{
    font-size: 30px;
    text-transform: uppercase;
}
.rekry-btm hr {
    width: 70%;
}
.rekry-btm p{
    color:#000;
    font-size: 18px;

}
.green{
    color:#4cb847;
    font-weight: 800;
}


@media only screen and (max-width:600px) {
    .rekry-head h2,h1 {
    width: 80%;
    }
    .rekry-btm {
        padding: 20px;
    }

}


