.taustaa-head {
    text-align: center;
}
.taustaa-head h2 {
    color:  #4cb847;
    font-weight: 400;
    margin: 0;
    padding-top: 50px;
}
.taustaa-head hr {
    border: solid 1px #000;
    width: 20%;
}
.taustaa-head p{
    font-style: italic;
    padding-bottom: 30px;
    font-size: 18px;

}
.information  {
    margin: 0 200px auto;
}

.exp-info{
    background-color: #4cb847; 
    padding-top: 20px;
    text-align: center;
}
.exp-box{
    width: 25%;
}


.exp-box p {
    color: #fff;
    font-size: 18px;
    font-style: italic;
    font-weight: 600;
}
.exp-box h3 {
    font-size: 72px;
    font-weight: 400;
}

.taustaa-btm{
    padding: 20px;
}
.taustaa-btm p{
    color: #4cb847;
    text-align: center;
    font-style: italic;
    line-height:2;
    background-image: url("./images/box.png");
    padding: 50px 20px 20px 20px;
    background-repeat: no-repeat; 
    background-size: 100% 100%;
    font-size: 20px;
}
@media only screen and (max-width:1500px) and (min-width:1041px) {
.information {
    margin: 0 45px auto ;
}
.exp-box p{
    font-size: 16px;
}


}

@media only screen and (max-width:1040px) and (min-width:769px) {
    .information {
        margin: 0;
    }
    .exp-box h3 {
        font-size: 48px;
    }
    .exp-info {
        padding-left: 0;    
    }
    .exp-box {
        display: inline-block;
    }
    .exp-box p{
        font-size: 13px;
    }
}
@media only screen and (max-width:850px) {
    .information {
        margin: 0 auto;
    }
    .exp-box {
        padding: 40px 15px;
    }
    .exp-box p{
        font-size: 14px;
    }
    .exp-box h3 {
        font-size: 48px;
    }
    .exp-box {
        display: inline-block;
        width: 47%;
    }
    .exp-info {
        padding-left: 5px;
        padding-right: 5px;
    }
    .exp-info p{
        padding: 0;
    }
    .taustaa-head hr {
        width: 50%;
    }
    .taustaa-btm {
        width: 100%;
        padding: 0;
    }
    .taustaa-btm p{
        background-color: #000; 
        font-size: 12px;
    }
    .taustaa-head p{
        font-size: 12px;
    }
}



