.container-fluid {
    width: 80%;
}
.contact-head {
    padding: 50px;
}
.contact-head hr {
    width: 15%;
}

.kaynti h2 {
    text-align: center;
    color: #4cb847; 
    font-weight: 400;
    font-size: 24px;
}
.kaynti p{
    text-align: center;
    font-style: italic;
    font-size: 16px;
}
.kaynti .info-bold {
    font-weight:800;
}
.map {
    text-align: center;
    margin: 10px;
}
iframe {
    width: 900px;
    height: 450px;
    border: solid 2px #4cb847;
    -webkit-box-shadow: 5px 5px #888888;
            box-shadow: 5px 5px #888888;
}

@media only screen and (max-width:1025px) and (min-width:769px) {
.container-fluid {
    width: 100%;
}
}
@media only screen and (max-width:768px) and (min-width:600px) {
    .container-fluid{
        width: 100%;
    }
    .kaynti h2{
        font-size: 27px;
    }
    .kaynti .subhead {
        font-size: 16px;
    }
    iframe {
        width: 100%;
    }
}
@media only screen and (max-width:600px) and (min-width:320px) {
    .container-fluid{
        width: 100%;
    }
    .contact-head hr {
        width: 100%;
    }
    .kaynti h2{
        font-size: 24px;
    }
    .kaynti {
        padding-bottom: 10px;
    }
    .kaynti .subhead {
        font-size: 12px;
    }
    iframe {
        width: 100%;
    }

}


