.team .row {
    display: table;
    border-spacing: 30px 0px;
}

.team .row [class*="col-"] {
    float: none;
    display: table-cell;
    vertical-align: top;
    
}
.team {
    width: 60%;
    margin: 0 auto;
    -webkit-box-sizing: border-box;
            box-sizing: border-box;
    margin-bottom: 10px;
   
}

.member img {
    width: 100%;
    text-align: center;
    background: #4cb847;
}
.member-top .member {
    width: 40%;
    margin-bottom: 50px;
    
}

.member-top .member:hover {
    width: 42%;   
}
.member-btm {
    background: #4cb847;
    width: 100%;
    padding: 60px;
}
.member {
    text-align: center;
    background: #000;
    color: #fff;
    padding: 0;
    margin: 0 20px auto ;
    width: 28.5%;
    -webkit-transition: width 0.5s;
    -o-transition: width 0.5s;
    transition: width 0.5s;
    text-align: center;
    padding: 80px 0;
}
.member:hover {
    width: 31%;
    -webkit-transition-timing-function: linear;
         -o-transition-timing-function: linear;
            transition-timing-function: linear;
    
}
.member h2, p{
    padding: 5px;
    margin: 0;
}
.member p{
    font-size: 14px;
}
.member-mail {
    color: #4cb847; 
}

@media only screen and (max-width:1300px) and (min-width:1050px) {
    .member-top .container {
        width: 80%;
    }
    
    .member-top {
        width: 85%;
    }
    .member {
        width: 27.5%;
    } 
    .member h2 {
        font-size: 21px;
    }
    .member p{
        font-size: 11px;
    }
    .member:hover {
        width: 29%;
    }
}
@media only screen and (max-width:1050px) and (min-width:769px) {
    .member-top .container {
        width: 100%;
    }
    .member-top .container-fluid {
        width: 100%;
    }
    .member-top {
        width:100%;
    }
    .member-top .member {
        width:40%;
    }
    .team .row {
        border-spacing: 8px 0px;
    }
    .member-btm {
        padding: 10px 0;
    }
}
@media only screen and (max-width:768px)  {
    .team .row [class*="col-"] { 
        display: block;
    }
    .team .row {
        border-spacing: 0px 1px;
      }
    .container{
        width: 100%;
    }
    .container-fluid {
        width: 100%;
        padding: 0;
    }
    .member {
        width: 100%;
        margin:0 0 10px 0;
    }
    .member-top .member{
        width: 100%;
        margin: 0;
        margin-bottom: 10px;     
    }
    .member-top {
        width: 100%;
        margin-bottom: -10px;
    }
    .member-btm {
        padding: 0;
        background: none;
    }
    .member:hover {
        width: 100%;
        -webkit-transition-timing-function: none;
             -o-transition-timing-function: none;
                transition-timing-function: none;
        
    }
    .member-top .member:hover {
        width: 100%;       
    }
    .container {
        width: 100%;
    }
    .member p{
        padding: 25px;
    }

}


