
p,h1,h2,h3,h4,h5,h6 {
	font-family: Source Sans Pro;
}
	
body {
	background-color:#fefefd;
}
.clearfix {
	clear:both;
}
.mission-head h2{
	color: #4cb847;
	text-align: center;
	font-weight: 600;
	padding-top: 40px;
}
hr{
	width: 50%;
	color: #4cb847;
	border: solid 1px #4cb847;
}
.subhead {
	text-align: center;
	font-style: italic;
	font-weight: 600;
}
.idea img{
    width: 100%;
}
.infobox h2{
	color: #000;
	background-color: #4cb847; 
	text-align: center;
	width: 70%;
	padding: 10px;
	float: left;
    margin-left: 33px;
    font-size: 26px;
}
.infobox {
    padding-top: 43px;
    margin-bottom: 5px;

}
.infobox p{
	background-color:#000; 
	color: #fff;
	padding: 78px 20px 43px 20px;
	margin-top: 35px;
	font-size: 16px;
	text-align: center;
}
.missio1 h2 {
	width: 60%;
}
.missio2 h2 {
	width: 75%;
}
.missio2 p {
    height:238px;
}

@media only screen  and (max-width:1300px)and (min-width:1025px) {
    .idea img {
        padding-top: 40px;
    }

}

@media only screen  and (max-width:1024px)and (min-width:768px) {

    .infobox h2 {
        font-size: 18px;
    }
    .infobox p{
        font-size: 13px;
    }
    .idea img {
        padding-top: 60px;
    }
    .row{
        margin: 0;
    }
    .missio2 p {
        height:175px;
    }
    .mission-head h2 {
        font-size: 22px;
    }
}
@media only screen and (max-width:767px)and (min-width:600px) {
    .infobox h2 {
        font-size: 19px;
    }
    .container {
        width: 100%;
    }
    .idea img {
        padding-top: 65px;
    }
    .infobox {
        width: 100%;
    }
    .missio2 p {
        height:auto;
    }
}
@media only screen and (max-width:500px) and (min-width:320px) {

    .mission-head h2{
        font-size: 16px;
    }
    .infobox {
        width: 100%;
    }
    .infobox h2{
        font-size: 16px;
    }
    .subhead {
        font-size: 12px;
    }
    .infobox p{
        font-size: 12px;
    }
    .container {
        padding: 0;
        margin: 0;
    }

    .missio2 p {
        height:auto;
    }
}



