.tavoitteet h2 {
    color:  #4cb847;
    text-align: center;
    padding-top: 20px;
    font-weight: 400;
    font-size: 40px;
}
.tavoitteet {
    margin-bottom: 20px;
    margin-top: 20px;
}
.tavoitteet hr {
        border-bottom: solid 1px #000;
        width: 30%;
        margin-top: 0;
}
.tavoitteet p {
    color: #4cb847;
    text-align: center;
    font-style: italic;
    background-image: url("./images/box.png");
    padding: 50px;
    background-repeat: no-repeat; 
    background-size: 100% 100%;
    font-size: 22px;
}
@media only screen and (max-width:1200px) and (min-width: 1024px) {
    .tavoitteet p{
        font-size: 20px;
        margin-top: 35px;
    }
}

@media only screen and (max-width:1023px) {
    .tavoitteet p{
        font-size: 14px;
    }
}

@media only screen and (max-width:600px) {
    .tavoitteet h2 {
        font-size: 24px;
    }
    .tavoitteet p{
        font-size: 12px;
        background: #000;
    }

}


